@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .bg-gradient-blue {
    background: linear-gradient(
      90deg,
      #0067a8 0%,
      #0091a6 55.29%,
      #00b3df 100%
    );
  }

  .bg-gradient-blue-hover {
    background: linear-gradient(
      90deg,
      #0067a8 0%,
      #0091a6 55.29%,
      #00b3df 100%
    );
  }
  .bg-gradient-blue-hover::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      -90.01deg,
      #0067a8 0.01%,
      #0091a6 55.29%,
      #00b3df 99.99%
    );

    z-index: -1;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  .bg-gradient-blue-hover:hover::before {
    opacity: 1;
  }

  /* Mobile Menu Styles */
  .burger-line-1,
  .burger-line-2,
  .burger-line-3 {
    transform-origin: center;
  }

  /* Prevent scrolling when menu is open */
  body.menu-open {
    overflow: hidden;
  }

  /* Mobile menu backdrop blur effect */
  #mobile-menu-overlay {
    backdrop-filter: blur(4px);
  }

  /* Smooth scrollbar for mobile menu */
  #mobile-menu {
    scrollbar-width: thin;
    scrollbar-color: rgba(15, 55, 71, 0.3) transparent;
  }

  #mobile-menu::-webkit-scrollbar {
    width: 4px;
  }

  #mobile-menu::-webkit-scrollbar-track {
    background: transparent;
  }

  #mobile-menu::-webkit-scrollbar-thumb {
    background-color: rgba(15, 55, 71, 0.3);
    border-radius: 2px;
  }

  /* Hide scrollbar for horizontal scroll */
  .scrollbar-hide {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }

  .scrollbar-hide::-webkit-scrollbar {
    display: none; /* Chrome, Safari and Opera */
  }
}

